import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

export class Navlink extends Component {

    renderLink = () => {

        if(this.props.clicked){

            return (

                <NavLink to={this.props.link} className="nav-link clicked-nav-link" >
                    <div className="nav-link-content">
                        {this.props.name}
                    </div>
                </NavLink> 
            )

        } else {

            return (

                <NavLink to={this.props.link} className="nav-link">
                    <div className="nav-link-content">
                        {this.props.name}
                    </div>
                </NavLink> 
           )
        }
    }

    render() {
        return (
           this.renderLink()
        )
    }
}

export default Navlink
