import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Header from './Components/Presentation/Header/header';
import Footer from './Components/Presentation/Footer/footer';
import HomePage from './Components/Pages/HomePage/homepage';
import Blog from './Components/Pages/Blog/blog';
import BlogContent from './Components/Presentation/BlogContent/blogcontent';

export class App extends Component {

  state = {

    page: "home",
    language: "portuguese",
    currentPage: "home"
  }

  links = [

    {displayNamePt : "Inicio", displayNameEn : "Home", name : "home", ptRoute: "/", enRoute: "/en"},
    {displayNamePt : "Blog", displayNameEn : "Blog", name : "blog", ptRoute: "/blog", enRoute: "/en/blog"}
]

  setPage = (page) => {
    this.setState({currentPage: page});
  }

  setLanguage = (language) => {
    this.setState({language});
  }

  changeLanguage = (language) => {
    this.setState({language});
  }

  changePage = (pageNumber) => {

    this.setState({currentPage: pageNumber});
}

  render() {
    return (
      <Router>
          <Header 
            currentPage={this.state.currentPage} 
            language={this.state.language}
            setLanguage={this.setLanguage}
            links={this.links}
          ></Header>
          <Switch>

            <Route 
              exact path="/" 
              render={props => <HomePage {...props} language={"portuguese"} setPage={this.setPage} setLanguage={this.setLanguage}></HomePage>} 
            />

            <Route 
              path="/blog" 
              render={props => <Blog {...props} language={"portuguese"} setPage={this.setPage} setLanguage={this.setLanguage}></Blog>}
            />

            <Route 
              path="/post/:slug" 
              render={props => <BlogContent {...props} language={"portuguese"} setPage={this.setPage} setLanguage={this.setLanguage}></BlogContent>}
            />

            <Route 
              exact path="/en" 
              render={props => <HomePage {...props} language={"english"} setPage={this.setPage} setLanguage={this.setLanguage}></HomePage>} 
            />

            <Route 
              path="/en/blog" 
              render={props => <Blog {...props} language={"english"} setPage={this.setPage} setLanguage={this.setLanguage}></Blog>}
            />

            <Route 
              path="/en/post/:slug" 
              render={props => <BlogContent {...props} language={"english"} setPage={this.setPage} setLanguage={this.setLanguage}></BlogContent>}
            />

            <Route 
              exact path="/en/:anything" 
              render={props => <HomePage {...props} language={"english"} setPage={this.setPage} setLanguage={this.setLanguage}></HomePage>} 
            />

            <Route 
              exact path="/:anything" 
              render={props => <HomePage {...props} language={"portuguese"} setPage={this.setPage} setLanguage={this.setLanguage}></HomePage>} 
            />
          </Switch>
          <Footer></Footer>
      </Router>
    )
  }
}

export default App
