import React, { Component } from 'react';
import Navlink from './navlink';
import LanguageSwitch from './languageswitch';

export class Navbar extends Component {

    isClicked = (name, currentPage) => {

        if(currentPage === name){
            return true;

        } else if (currentPage === "post" & name === "blog"){
            return true;

        } else {
            return false;
        }
    }

    render() {
        return (
            <div id="header-nav">
                {
                
                    this.props.links.map(navlink => {

                        if(this.props.language === "portuguese"){

                            return (

                                <Navlink 
                                    name={navlink.displayNamePt}
                                    key={navlink.name}
                                    currentPage={this.props.currentPage}
                                    clicked={this.isClicked(navlink.name, this.props.currentPage)}
                                    link={navlink.ptRoute}
                                ></Navlink>
                            )

                        } else {

                            return (

                                <Navlink 
                                    name={navlink.displayNameEn}
                                    key={navlink.name}
                                    currentPage={this.props.currentPage}
                                    clicked={this.isClicked(navlink.name, this.props.currentPage)}
                                    link={navlink.enRoute}
                                ></Navlink>
                            )
                        }
                    })
                }
                <LanguageSwitch 
                    language={this.props.language}
                    currentPage={this.props.currentPage}
                    setLanguage={this.props.setLanguage}
                ></LanguageSwitch>
            </div>
        )
    }
}

export default Navbar
