import React, { useEffect } from 'react';
import gql from 'graphql-tag';
import { graphql } from 'react-apollo';
import Markdown from 'react-markdown';
import SideContent from '../SideContent/sidecontent';
import './blogcontent.css';
import { Link } from 'react-router-dom';
import Helmet from 'react-helmet';
import Disqus from "disqus-react";

const getPostEndpoint = (language) => {
  return language === "portuguese"? "http://rodrigogomesrc.github.io/post/" : "http://rodrigogomesrc.github.io/en/post/";
}

const renderMetaTags = (language, post) => {

  if(language === "portuguese") {

    return (

      <Helmet>

        <title>{post.title}</title>
        <meta http-equiv="content-language" content="pt-br"/>
        <meta name="description" content={post.description} ></meta>
        <meta property="og:site_name" content="Rodrigo Rocha Blog"></meta>
        <meta property="og:description" content={post.description} ></meta>
        <meta property="og:title" content={post.title} ></meta>
        <meta property="og:type"  content="article" ></meta>
        <meta property="og:url"   content={"https://rodrigogomesrc.github.io/post/" + post.url} ></meta>
        <meta property="og:image" content={`https://media.graphcms.com/resize=w:800,h:418,fit:crop/${post.coverImage.handle}`} ></meta>
        <meta property="og:image:alt"    content="Post Cover" ></meta>
        <meta property="og:image:height" content="418" ></meta>
        <meta property="og:image:type"  content="image/png" ></meta>
        <meta property="og:image:width" content="800" ></meta>
        <meta property="article:author" content="Rodrigo Rocha"></meta>
        <meta property="og:locale" content="pt_BR" ></meta>
        <meta property="og:locale:alternate" content="es_US" ></meta>

      </Helmet>
    )

  } else {

    return (

      <Helmet>

        <title>{post.title}</title>
        <meta http-equiv="content-language" content="en-us"/>
        <meta name="description" content={post.description} ></meta>
        <meta property="og:site_name" content="Rodrigo Rocha Blog"></meta>
        <meta property="og:description" content={post.description} ></meta>
        <meta property="og:title" content={post.title} ></meta>
        <meta property="og:type"  content="article" ></meta>
        <meta property="og:url"   content={"https://rodrigogomesrc.github.io/en/post/" + post.url} ></meta>
        <meta property="og:image" content={`https://media.graphcms.com/resize=w:800,h:418,fit:crop/${post.coverImage.handle}`} ></meta>
        <meta property="og:image:alt"    content="Imagem ilustrativa do post" ></meta>
        <meta property="og:image:height" content="418" ></meta>
        <meta property="og:image:type"  content="image/png" ></meta>
        <meta property="og:image:width" content="800" ></meta>
        <meta property="article:author" content="Rodrigo Rocha"></meta>
        <meta property="og:locale" content="en_US" ></meta>
        <meta property="og:locale:alternate" content="pt_BR" ></meta>

      </Helmet>
    )
  }
}

const BlogContent = ({ data: { loading, error, post}, language, setPage, setLanguage}) => {

  // to work as ComponentDidMount
  useEffect(() => {
    setPage("post");
    setLanguage(language);
    // eslint-disable-next-line
  }, [])

  if (error) {
      return (
          
        <div className="container">
          {
            language === "portuguese"? <h2>Algum erro ocorreu</h2> : <h2>Something went wrong</h2>
          }
        </div>
      )
  }
  if (!loading && post) {

    //DEFINE THIS BY LANGUAGE
    const disqusShortname = "rodrigogomesrc";
    const disqusConfig = {
      url: getPostEndpoint(language) + post.url, 
      identifier: post.id,
      title: post.title
    }
    
    return (

        <div className="post-main">

            {
              renderMetaTags(language, post)
            }

            <div id="blog-content">
                <div id="blog-content-container">
                    <article>
                        {
                          language === "portuguese"?
                          <Link to={"/blog"} className="page-link blog-back">Ir para os Posts</Link> :
                          <Link to={"/blog"} className="page-link blog-back">Go to Posts</Link>
                        }
                        <h1 className="title post-title" >{post.title}</h1>
                        <h3 className="post-description">{post.description}</h3>
                        <p className="date">{post.date}</p>
                        <div className='Post-placeholder'>
                        <img
                            className="cover-image"
                            alt={post.title}
                            src={`https://media.graphcms.com/resize=w:650,h:366,fit:crop/${post.coverImage.handle}`}
                        />
                        </div>

                        <Markdown
                          source={post.content}
                          escapeHtml={true}
                        />
                        <div className="comments">
                          <Disqus.DiscussionEmbed
                            shortname={disqusShortname}
                            config={disqusConfig}
                          />
                        </div>

                    </article>
                </div>
            </div>
            <SideContent></SideContent>
        </div>
    )
  }
  else if(!loading && !post){

    return (
      <div className="container">
        {
          language === "portuguese"? 
          <h1>Ops! Parece que esse post não existe.</h1> : 
          <h1>Ops! It looks like this post doesn't exist.</h1>
        }
      </div>
    )
  }
  
  return(
      <div className="container">
        {
          language === "portuguese"? <h1>Carregando o post...</h1> : <h1>Loading post...</h1>
        }
      </div>
  )
} 

export const singlePost = gql`
query singlePost($url: String) {
    post(where: {url: $url}) {
      id
      title
      description
      url
      date
      coverImage {
        id
        handle
      }
      content
      createdAt
    }
  }
`

export default graphql(singlePost, {
  options: ({ match }) => ({
    variables: {
      url: match.params.slug
    }
  })
})(BlogContent)