import React, { Component } from 'react';
import './homepage.css';
import PageCover from '../../Presentation/PageCover/pagecover';
import HomeMain from './../../Presentation/HomeMain/homemain';

export class HomePage extends Component {


    componentDidMount() {
        this.props.setPage("home");
        this.props.setLanguage(this.props.language);
    }

    render() {

        return (
            <div id="home-page-holder">
               <PageCover language={this.props.language} content={this.props.content}></PageCover>
               <HomeMain language={this.props.language} content={this.props.content}></HomeMain>
            </div>
        )
    }
}

export default HomePage
