import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export class LanguageSwitch extends Component {


    redirectionLink = (currentPage, choosenLanguage) => {
    
        var url = "";    
        switch (currentPage) {
            case "home":
                url = choosenLanguage === "portuguese"? "/" : "/en";
                break;

            case "blog":
                url = choosenLanguage === "portuguese"? "/blog" : "/en/blog";
                break;
            
            case "post":
                url = choosenLanguage === "portuguese"? "/blog" : "/en/blog";
                break;
    
            default:
                url = choosenLanguage === "portuguese"? "/" : "/en";
                break;
       }
       return url;
    }

    renderButtons = () => {

        if(this.props.language === "portuguese"){
            
            return (

                <div id="language-switch">
                    <Link to={this.redirectionLink(this.props.currentPage, "portuguese")}>
                        <div className="language clicked-language" onClick={this.props.setLanguage.bind(this, "portuguese")}>
                            <div className="language-name">
                                Pt-BR
                            </div>
                        </div>
                    </Link>
                   
                    <Link to={this.redirectionLink(this.props.currentPage, "english")}>
                        <div className="language" onClick={this.props.setLanguage.bind(this, "english")}>
                            <div className="language-name">
                                En
                            </div>
                        </div>
                    </Link>
                </div>
            )

        } else {

            return (

                <div id="language-switch">
                <Link to={this.redirectionLink(this.props.currentPage, "portuguese")}>
                    <div className="language" onClick={this.props.setLanguage.bind(this, "portuguese")}>
                        <div className="language-name">
                            Pt-BR
                        </div>
                    </div>
                </Link>
               
                <Link to={this.redirectionLink(this.props.currentPage, "english")}>
                    <div className="language clicked-language" onClick={this.props.setLanguage.bind(this, "english")}>
                        <div className="language-name">
                            En
                        </div>
                    </div>
                </Link>
            </div>
            )
        }
    }

    render() {

        return (
            this.renderButtons()
        )
    }
}

export default LanguageSwitch
